.users {
    background-color: #edeef0;
}
.user {
    background-color: #fff;
    display: flex;
    padding: 10px 0;
    border-bottom: 1px solid rgba($color: #000000, $alpha: 0.12);
    &:last-child {
        border-bottom: 1px solid transparent;
    }
}
.inner {
    padding: 0 15px;
    background: #fff;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}
.avatar {
    display: flex;
    flex-flow: column;
    align-items: center;
    margin-right: 20px;
    img {
        width: 80px;
        height: 80px;
        object-fit: cover;
        border-radius: 50%;
    }
}
.content {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.name {
    font-size: 17px;
    font-weight: 600;
    text-decoration: none;
    color: #000000;
}
.city {
    font-size: 16px;
    font-weight: 300;
}
.first {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
